};

// 查看详情
const viewDetail = (record) => {
  // 获取当前评估人信息
  evaluatorNow.value = record.Evaluator;
  evaluatorDepartmentNow.value = record.department;
  evaluatorNowUserId.value = record.userId;
  // 调用接口获取当前评估人的打分详情
  getScoreDetailsData(record.userId, orderInfo.condition, orderInfo.orderBy);
  // 打开弹窗
  openTable.value = true;
};

// 编辑驳回数据
const rejectTaskId = ref([]); // 被驳回的任务列表
const rejectReasons = ref([]); // 驳回理由列表
// 点击“驳回”按钮
const toggleReject = (record) => {
  // 切换驳回状态
  const index = rejectTaskId.value.indexOf(record.taskId);

  if (index === -1) {
    // 如果当前任务没有被驳回，变为可编辑状态
    rejectTaskId.value.push(record.taskId);
    rejectReasons.value.push({ taskId: record.taskId, reason: "" });
    // 设置该行可编辑
    record.rejectReasonEditable = true;
  } else {
    // 如果已经驳回，弹出确认框确认是否取消驳回
    record.rejectReasonEditable = false;
    rejectTaskId.value.splice(index, 1); // 删除该任务id
    rejectReasons.value = rejectReasons.value.filter(
      (item) => item.taskId !== record.taskId
    ); // 删除理由
  }
};

// 判断是否已驳回
const isRejected = (record) => rejectTaskId.value.includes(record.taskId);

// 输入拒绝理由
const handleRejectReasonChange = (value, record) => {
  const reasonIndex = rejectReasons.value.findIndex(
    (item) => item.taskId === record.taskId
  );
  if (reasonIndex !== -1) {
    rejectReasons.value[reasonIndex].reason = value;
  }
};

const isRejectReasonOpen = ref(false);
const rejectReason = ref("");
const openRejectModal = () => {
  isRejectReasonOpen.value = true;
};
// 提交驳回数据
const submitReject = () => {
  // 获取发送到后端的taskId和rejectReason
  const tasksToReject = dataScoreDetails.value.map((item) => item.taskId);
  const reasonsToReject = new Array(tasksToReject.length).fill(
    rejectReason.value
  );

  // 调用后端接口提交数据
  getRejectScoreApi(tasksToReject, reasonsToReject)
    .then((res) => {
      if (res.data.code === 200) {
        message.success("已成功驳回!");
        isRejectReasonOpen.value = false;
        openTable.value = false;
        getRateTableData();
      } else {
        message.error("驳回失败，" + res.data.msg);
      }
    })
    .catch((err) => {
      message.error("请求失败！"+ err);
    });
};

// 关闭查看详情弹窗
const closeDetailModal = () => {
  getRateTableData();
};

// 确认评分
const confirmRating = (record) => {
  getConfirmsSingleScoreApi(record.userId)
    .then((res) => {
      if (res.data.code === 200) {
        message.success("已确认" + record.Evaluator + "的评分! ^_^");
        getRateTableData();
      } else {
        message.error("确认失败，" + res.data.msg);
      }
    })
    .catch((err) => {
      message.error("请求失败！"+ err);
    });
};

// 批量确认
const handleManyConfirm = () => {
  // 在确认之前判断是否有选择项
  if (state.selectedRowKeys.length === 0) {
    message.warning("请先选择评估人！");
    return;
  }

  getConfirmsManyScoreApi(state.selectedRowKeys)
    .then((res) => {
      if (res.data.code === 200) {
        // 显示成功消息
        message.success(
          `已确认${state.selectedRowKeys.length}位评估人的评分! ^_^`
        );

        // 清空选择项
        state.selectedRowKeys = [];

        // 重新获取评分表格数据
        getRateTableData();
      } else {
        message.error("操作失败，" + res.data.msg);
      }
    })
    .catch((err) => {
      message.error("请求失败！" + err);
    });
};

const openTable = ref(false);
const dataScoreDetails = ref([]);
const evaluatorNow = ref("");
const evaluatorDepartmentNow = ref("");
const evaluatorNowUserId = ref("");

// 打分详情表格页表头配置
