  getNoticeApi()
    .then((res) => {
      if (res.data.code === 200) {
        message.success("提醒成功！");
      } else {
        message.error("提醒失败！" + res.data.msg);
      }
    })
    .catch((err) => {
      console.log("获取失败！", err);
    });
};

