  { value: 3, name: '创新求实 (I)' },
  { value: 4, name: '信任互助 (H)' },
  { value: 5, name: '尊重平等 (R)' },
  { value: 6, name: '业精于勤 (D)' },
  { value: 7, name: '使命必达 (M)' },
  { value: 8, name: '五湖四海 (F)' },
  { value: 9, name: '坚持自省 (I)' },
]);
