  updateScrollHeight();
  window.addEventListener("resize", updateScrollHeight);
});

// 在组件卸载时移除事件监听
onUnmounted(() => {
  window.removeEventListener("resize", updateScrollHeight);
});
</script>

<template>
  <div v-if="isVisible" class="rate-board">
    <!-- 表格顶部区域 -->
    <div class="content-header">
      <!-- 搜索表单 -->
      <div class="search-form">
        <a-form style="display: flex; align-items: center">
          <a-form-item label="评估人" style="margin-right: 20px; width: 200px">
            <a-input
              placeholder="请输入姓名"
              v-model:value="serchInfo.name"
              :allowClear="true"
            />
          </a-form-item>
          <a-form-item label="部门" style="margin-right: 20px; width: 200px">
            <a-select
              placeholder="请选择部门"
              v-model:value="serchInfo.department"
              :allowClear="true"
            >
              <a-select-option
                v-for="item in departmentList"
                :key="item"
                :value="item"
              >
                {{ item }}
              </a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item label="状态" style="margin-right: 20px; width: 200px">
            <a-select
              placeholder="请选择状态"
              v-model:value="serchInfo.status"
              :allowClear="true"
            >
              <a-select-option value="">全部</a-select-option>
              <a-select-option value="待确认">待确认</a-select-option>
              <a-select-option value="已确认">已确认</a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item>
            <a-button
              type="primary"
