          style="margin-right: 10px"
        >
          <template #icon>
            <FormOutlined />
          </template>
          批量确认
        </a-button>
        <a-button
          v-if="isRateBoard"
          type="primary"
          style="background-color: #68bbc4"
          @click="getProcess()"
        >
          进入下一环节
        </a-button>
        <a-button v-if="!isRateBoard" type="primary" disabled>
