import axios from "axios";

//获取评分看板数据
const getRateDataApi = (pageNum, pageSize, name, department, state) => new Promise((resolve, reject) =>{
    let data = {
        pageNum: pageNum,
        pageSize: pageSize,
        name: name,
        department: department,
        state: state
      };
    axios({
        method:'post',
        url:'/api/average/score',
        headers:{
            "Content-Type": "application/json",
        },
        data: JSON.stringify(data),
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

//查看某人打分详情
const getRateDetailApi = (id, condition, orderBy) => new Promise((resolve, reject) =>{
    axios({
        method:'get',
        url:'/api/average/score/detail',
        headers:{
            "Content-Type": "application/json",
        },
        params: {
            id: id,
            condition: condition,
            orderBy: orderBy
        },
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

